@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
:root{
    --bg: #f4f1de;
    --orange: #e07a5f;
    --dark: #3d405b;
    --green: #81b29a;
    --yellow: #f2cc8f;
}

body{
    background: #3d405b;
    background: var(--dark);
    color: #f4f1de;
    color: var(--bg);
    font-size: 16px;
    font-family: 'Lato', sans-serif;
    display: grid;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}
.title--label{
    font-size:2rem;
    color: peru;
    font-weight: 700;
    white-space: pre-line;
    text-transform: uppercase;
    word-spacing: 0.5rem;
    letter-spacing: .5rem;
    text-align: center;
}

    .title__description{
        font-size: .85rem;
        font-weight: 300;
    }
.login__input{
    border: 1px solid var(--dark);
    border-radius: 0.45rem;
    box-sizing: border-box;
    color: var(--green);
    margin: 0.5rem 0;
    padding: .85rem 1.5rem;
    outline: none;
    width: 100%;
}
    .login__input:focus, .login__input:active{
        border: 1px solid var(--green);
    } 
.input--error{
    border: 1px solid var(--orange);
    color: var(--yellow);
}

.login__label{
    text-align: start;
}
.login{
    background: var(--bg);
    border-radius: .5rem;
    color: var(--dark);
    display: flex;
    flex-direction: column;
    height: 80vh;
    justify-content: center;
    min-width: 350px;
    max-width: 400px;
    padding: 1.5rem 1rem;
    width: 100%;
}


.btn{
    border: none;
    background: var(--green);
    border-radius: .25rem;
    color: white;
    display: block;
    margin: 1rem 0;
    padding: .5rem 1rem;
    font-size: 1.1rem;
    width: 100%;
    transition: all .5s;
}
    .btn:hover, .btn:focus, btn:active{
        background: var(--orange);
        border: none
    }

.label--error{
    background: var(--orange);
    border-radius: 0.25rem;
    box-sizing: border-box;
    color: var(--bg);
    padding: .5rem 1.5rem;
    width: 100%;
}
